@import url(https://fonts.googleapis.com/css?family=Cute+Font&display=swap);
body {
  margin: 0;
  background: url(/React-speedgame/static/media/jason-leung-Xaanw0s0pMk-unsplash.0f513f0e.jpg) no-repeat center center fixed;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

h1 {
  color: white;
  font-family: 'Cute Font', cursive;
  font-size: 4em;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;

}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

button {
  background-color: white;
  color: orangered;
  border: solid 1px white;
  padding: 10px;
  margin: 20px;
  border-radius: 5%;
}

p {
  color: white;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.circle {
    height: 150px;
    width: 150px;
    border-radius: 50%;
    display: inline-block;
    margin: 20px;
    background: #B82F00;
}

.circle.active {
    box-shadow: inset 0px 0 px 15 px rgba(0, 0, 0, 1);
    background: white;
}

#result {
    position: fixed;
    background-color: black;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.gameoverbox {
    height: 300px;
    width: 300px;
    background-color: white;
    opacity: 1;
    border: solid 1px white;
    border-radius: 5%;
}
